import { Observable } from 'rxjs/Observable';
import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ConfigService } from './config.service';
import { ChatService } from './chat.service';
import { ProductModel } from '../interfaces/product.interface';

/**
 * 
 */

@Injectable()
export class User{
    //
    public user:string
    //
    public isLogin:boolean
    //
    public msgCount:number = 0
    //
    public headImg:string = ""
    //
    public money:number = 0
    //
    public sex:string = "null"
    //
    public age:number = -1
    //
    public point:number = 0
    //
    public level:number = 0
    //
    public pointView:number = 0
    //
    public tools:ProductModel[] = []

    constructor(
        private http:Http,
        private configService:ConfigService
    ){

    }

    //
    login(user:string):void{
        this.user = user;
        localStorage.setItem("user",user);
        this.isLogin = true;
    }

    //
    loginOut():void{
        this.user = null;
        localStorage.setItem("user",null);
        this.isLogin = false;
    }
   
    //
    getLoginState():boolean{
        return this.isLogin;
    }

    //
    getUserInfo():{user:string}{
        return {'user':this.user};
    }

    //
    getMsgCount(){
        return this.msgCount;
    }
    //
    addMsgCount(){
        this.msgCount++;
    }

    //
    getFriendList():Observable<Array<Object>>{
        return new Observable<any>(observer => {
            return this.http.post(this.configService.exportInterface("/getUser/") + this.user,{})
            .subscribe((res:Response) => {
                let data = res.json().successful[0];
                this.http.post(this.configService.exportInterface("/getFriendList"),{friendList:data.friends})
                .subscribe((res:Response) => {
                    let data = res.json()
                    observer.next(data.successful);
                })
            });
        })
    }

    //
    addFriend(targetUser:string, msg:string):Observable<Response>{
        return this.http.post(this.configService.exportInterface("/addFriend"),{user:this.getUserInfo().user,targetUser:targetUser, msg:msg});
    }

    //
    setFreeState(isFree:boolean):Observable<Response>{
        return this.http.post(this.configService.exportInterface("/setFreeState"),{user:this.user || localStorage.getItem("user"),isFree:isFree});
    }

    //
    getFreeState(targetUser:string):Observable<any>{
        return new Observable(observer => {
            this.http.post(this.configService.exportInterface("/getUser/") + targetUser,{})
            .toPromise()
            .then((res:Response) => {
                let data = res.json();
                if(data.successful){
                    observer.next({isFree:data.successful[0].isFree,isLine:data.successful[0].isLine});
                }
                else {
                    throw new Error("");
                }
            });
        });
    }

    //
    sendMsg(targetUser:string, msg:any, tag:string, user?:string){
        this.http.post(this.configService.exportInterface("/sendMsg"),{targetUser:targetUser, msg:msg, tag:tag, user:user || this.user})
            .subscribe(() => console.log(""))
    }

    //
    addFriendTrue(targetUser:string){
        this.http.post(this.configService.exportInterface("/addFriendTrue"),{targetUser:targetUser, user:this.user})
        .subscribe(() => {
            //
            this.sendMsg(this.user, "","addFriendSuccess")
            this.sendMsg(targetUser,"","addFriendSuccess")
        })
    }
    
    //
    listeners:Function[] = []
    /**
     * 
     * @param isListener 
     * @param cb 
     * @param isExecute 
     */
    updateUserProperty(isExecute?:boolean,isListener?:boolean, cb?:Function){
        console.log("...")
        this.http.post(this.configService.exportInterface("/getUserInfo"),{user:this.user})
            .subscribe((res:Response) => {
                let data = res.json()
                console.log(data)
                if(data.err) {
                    console.log(data.err)
                    return
                }
                data = data.successful[0]
                this.user = data.user
                this.headImg = data.headImg
                this.money = data.money
                this.sex = data.sex
                this.age = data.age
                this.point = data.point
                this.pointView = this.point % this.configService.LEVEL_INCREMENT
                this.level = parseInt(this.point / this.configService.LEVEL_INCREMENT + '') + 1
                this.tools = data.tools
                if(isExecute) {
                    //
                    for(let listener of this.listeners) {
                        listener()
                    }
                    return
                }
                //
                if(cb) {
                    if(!isListener) {
                        cb()
                        return
                    }
                    //
                    if(this.listeners.indexOf(cb) == -1) this.listeners.push(cb)
                    //
                    for(let listener of this.listeners) {
                        listener()
                    }
                }
            })
    }

    //
    /**
     * 
     * @param listener 
     * @param isExecute 
     */
    directPushListener(listener:Function, isExecute?:boolean){
        if(isExecute) listener()
        if(this.listeners.indexOf(listener) == -1) this.listeners.push(listener)
    }

    //
    uploadHeadImg(formData:FormData):Observable<Response>{
        return this.http.post(this.configService.exportInterface("/uploadHeadImg"),formData)
    }

    //
    saveUserInfo(obj:UserInfoDTO):Observable<Response>{
        obj['user'] = this.user
        return this.http.post(this.configService.exportInterface("/saveUserInfo"),obj)
    }

    //
    removeUserToMatch(){
        this.http.post(this.configService.exportInterface("/removeUserToMatch"),{user:this.user})
            .subscribe(() => {})
    }
}   


