import { SubscribeCollection } from './../../../services/subscribe-collection';
import { User } from './../../../services/user.service';
import { DialogClose } from './../../../dialog/dialog-close';
import { DialogModelSchema } from './../../../interfaces/dialog-schema.interface';
import { Observable } from 'rxjs/Observable';
import { ChatService } from './../../../services/chat.service';
import { Component, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfigService } from '../../../services/config.service';
import { Organize2 } from '../../../services/organize2.service';
import { HeadImgPipe } from '../../../pipes/headImgPipe.pipe';

@Component({
    selector:"tab",
    templateUrl:"./tab.component.html",
    styleUrls:["./tab.component.css"]
})
export class TabComponent extends DialogClose implements OnDestroy{

    constructor(
        private chatService:ChatService,
        private router:Router,
        private user:User, 
        private subscribeCollection:SubscribeCollection,
        private configService:ConfigService,
        private activatedRoute:ActivatedRoute,
        private organizeService:Organize2
    ){
        super();
    }

    //
    dialogModel:DialogModelSchema;

    //dialogmodel
    dialogInit(){
        this.showDialog({
            title:"",
            message:"...",
            buttonIndex:0,
            isViewImg:true
        });
    }

    //
    alone(){
        //
        this.dialogInit()
        //
        this.chatService.match(this.user.getUserInfo().user);
        //
        let observable:Observable<boolean> = this.chatService.matchSuccessful();
        this.subscribeCollection.addSubscription(
            "alone",
            observable.subscribe(data => {
            if(data){
                //
                this.close();
                //
                console.log("");
                this.router.navigate(['main/game-pane']);
            }
        }));
        
        //
        setTimeout(() => {
            //
            this.dialogModel.cancelMatchIsView = true
            this.dialogModel.cancelMatchFunc = () => {
                this.close()
                //
                this.user.removeUserToMatch()
            }
            
        },this.configService.MAX_MATCH_TIME)
    }

    //
    friend(){
        //
        this.organizeService.isCaptain = true
        this.organizeService.createOrganize(() => {
            this.router.navigate(['main/friend-game']);
        })
    }

    ngOnDestroy(){
        //
        this.subscribeCollection.unSubscription("alone");
    }

    shop(){
        this.router.navigate(['main/shop-pane/product'])
    }
}


