export class ConfigService{
    public readonly PROTOCAL:string = "http" //

    public readonly SERVER_HOST:string = "localhost" //
    public readonly SERVER_PORT:string = "3000" //
    
    public readonly AGE_ARR:number[] = [] //
    
    public readonly MAX_MATCH_TIME:number = 5000 //
    
    public readonly MAX_ORGANIZE_USER_COUNT:number = 4 //

    public readonly LEVEL_INCREMENT = 15 //
    
    public readonly MAX_TIME = 60 //

    constructor(){
        //202
        for(let i = -1;i <= 200;i++){
            this.AGE_ARR.push(i)
        }
    }

    //
    public exportInterface(path?:string):string{
        return `${this.PROTOCAL}://${this.SERVER_HOST}:${this.SERVER_PORT}${path || ""}`
    }
}


