import { Injectable } from "@angular/core";

/**
 * 
 */

@Injectable()
export class ActivityStack{
    //
    private stack:string[] = []

    //
    getStack():string[]{
        return this.stack
    }

    //url
    pushURL(url:string){
        //
        if(this.peek() == url) return
        //
        this.stack.push(url)
    }

    //url
    popURL():string{
        return this.stack.pop()
    }

    //
    peek():string{
        return this.stack[this.stack.length - 1]
    }
    
    //
    reset(){
        this.stack = []
    }
}


