import { DialogModelSchema } from './../../../../interfaces/dialog-schema.interface';
import { DialogClose } from './../../../../dialog/dialog-close';
import { DialogComponent } from './../../../../dialog/dialog.component';
import { SubscribeCollection } from './../../../../services/subscribe-collection';
import { User } from './../../../../services/user.service';
import { Component, Output, EventEmitter, Input, OnInit, OnDestroy, OnChanges } from '@angular/core';
import { Http, Response } from '@angular/http';


@Component({
    selector:"search-friend",
    templateUrl:"./search-friend.component.html",
    styleUrls:["search-friend.component.css"]
})
export class SearchFriendComponent extends DialogClose implements OnInit, OnDestroy, OnChanges{
    constructor(private http:Http, private user:User, private subscribeCollection:SubscribeCollection){
        super();
        console.log(typeof this.showDialog);
    }

    //
    isViewFriendList:boolean = false;
    //
    pName:string = "";
    //
    friendList:Array<any>;
    //
    @Input()
    searchName:string;
    @Output()
    hiddenSearchFriend:EventEmitter<String> = new EventEmitter<String>();
    //parentFriendList
    @Input()
    parentFriendList:Array<any>

    //
    addFriend(){

    }

    //
    close(){
        this.hiddenSearchFriend.emit();
    }

    viewDialog(dialogModel:DialogModelSchema){
        this.showDialog(dialogModel);
    }

    //
    ngOnInit(){
        //
        console.log(` ${this.searchName}`);
        if(this.searchName == "" || !this.searchName){
            this.isViewFriendList = false;
            return;
        }
        else if(this.searchName == this.user.getUserInfo().user){
            this.pName = "";
            return;
        }
        //
        this.subscribeCollection.addSubscription("queryUser",this.http.post("http://localhost:3000/getUser/" + this.searchName,{})
        .subscribe((res:Response) => {
            let data = res.json();
            //
            if(data.err){
                this.isViewFriendList = false;
            }
            //
            else{
                this.friendList = data.successful;
                this.isViewFriendList = true;
            }
        }));
    }

    ngOnDestroy(){
        this.subscribeCollection.unSubscription("queryUser");
        this.subscribeCollection.unSubscription("addFriend");
    }

    ngOnChanges(){
        this.addFriend = () => {
            //
            console.log(this.parentFriendList);
            for(let val of this.parentFriendList){
                if(val.user == this.searchName){
                    console.log(`user: ${val.user}  ${this.searchName}`);
                    this.viewDialog({
                        title:"",
                        message:"",
                        buttonIndex:1,
                        btn1:{title:"",func:() => {
                            this.close();
                        },color:"green"}
                    });
                    return;
                }
            }        

            //
            for(let val of this.friendList){
                if(val.user == this.searchName && val.isLine == false) {
                    this.viewDialog({
                        title:"",
                        message:"",
                        buttonIndex:1,
                        btn1:{title:"",func:() => {
                            //
                            this.close();
                        },color:"green"}
                    });
                }
            }
            //
            this.user.sendMsg(this.searchName, "", "addFriend")


            // this.subscribeCollection.addSubscription("addFriend",this.user.addFriend(this.searchName, null)
            // .subscribe((res:Response) => {
            //     let data = res.json();
            //     if(data.successful){
            //         this.viewDialog({
            //             title:"",
            //             message:"",
            //             buttonIndex:1,
            //             btn1:{title:"",func:() => {
            //                 this.close();
            //             },color:"green"}
            //         });
            //     }
            // }));
        }
    }
}


