import { Injectable } from "@angular/core";
import { Http, Response } from "@angular/http";
import { ConfigService } from "./config.service";
import { User } from "./user.service";
import { Observable } from "rxjs/Observable";

/**
 * 
 */

@Injectable()
export class Point{
    //
    readonly HOST_POINT = 2
    //
    readonly BY_AWAY_POINT = 3
    //
    readonly HOST_MONEY = 10
    //
    readonly BY_AWAY_MONEY = 15

    //
    private pointCache = 0
    //
    private moneyCache = 0

    constructor(
        private http:Http,
        private configService:ConfigService,
        private user:User
    ){

    }
    
    private readData(inter:string){
        return this.http.post(this.configService.exportInterface(inter),{user:this.user.user})
    }

    //
    readPoint():Observable<Response>{
        return this.readData("/readPoint")
    }
    //
    readMoney():Observable<Response>{
        return this.readData("/readMoney")
    }

    //
    updatePoint(){
        this.http.post(this.configService.exportInterface("/updatePoint"),{
            user:this.user.user,
            pointValue:this.pointCache
        })
        .subscribe(() => {
            //
            console.log(`${this.pointCache}`)
            this.resetPointCache()
        })
    }
    //
    updateMoney(){
        this.http.post(this.configService.exportInterface("/updateMoney"),{
            user:this.user.user,
            moneyValue:this.moneyCache
        })
        .subscribe(() => {
            //
            console.log(`${this.moneyCache}`)
            this.resetMoneyCache()
        })
    }

    //
    resetPointCache(){
        this.pointCache = 0
    }
    //
    resetMoneyCache(){
        this.moneyCache = 0
    }

    //
    addPointCache(pointValue:number){
        this.pointCache += pointValue
    }
    //
    addMoneyCache(moneyValue:number){
        this.moneyCache += moneyValue
    }    
}


