import { User } from './user.service';
import { Injectable } from '@angular/core';
import { Observable } from "rxjs/Observable";
import { ReadyQueue } from '../interfaces/ready-queue.interface';
import { GameDTO } from '../interfaces/game-dto.interface';
import { Http, Response } from '@angular/http';
import { ConfigService } from './config.service';
const io = require('socket.io-client');

/**
 * websocketservice
 */

@Injectable()
export class ChatService{
    private location:string = "http://localhost:3000";
    //Socket
    private socket:any;
    //
    private _roomID:number;
    //readyQueue 
    private _readyQueue:ReadyQueue[] = []


    constructor(
        private user:User, 
        private http:Http,
        private configService:ConfigService
    ){
    
    }
    
    set readyQueue(rq:ReadyQueue[]){
        this._readyQueue = rq
    }

    get readyQueue(){
        return this._readyQueue
    }

    set roomID(rid:number){
        this._roomID = rid
    }

    get roomID(){
        return this._roomID
    }

    //websocket
    connect(connection?:Function, disconnect?:Function){
        this.socket = io.connect(this.location);
        console.log(this.socket);
        //
        this.socket.on("connection",() => {
            if(connection) connection(this.socket);
            this.socket.emit("changeLine",{user:this.user.getUserInfo().user,isLine:true});
        })
        //
        console.log(` ${this.user.getUserInfo().user}`);
    }

    //
    disconnect(){
        //
        if(this.socket){
            //
            this.socket.emit("changeLine",{user:this.user.getUserInfo().user,isLine:false});
            //
            this.socket.disconnect();
            //GC
            this.socket = null;            
        }
    }

    //
    match(user:string){
        this.socket.emit("match",{user:user});
    }

    //socket
    getSocket(){
        return this.socket;
    }

    //
    matchSuccessful(){
        return new Observable<boolean>(observer => {
            this.socket.once("matchSuccessful",roomID => {
                this.roomID = roomID;
                observer.next(true);
            });
        });
    }

    //
    changeCanvas(dataURL:string){
        this.socket.emit("changeCanvas",{dataURL:dataURL,roomID:this.roomID});
    }

    //
    getUpdateCanvas(){
        //Canvas
        return new Observable<string>(observer => {
            this.socket.on("changeCanvasSuccessful",(dataURL:string) => {
                observer.next(dataURL);
            });
        });
    }

    //
    getQuestion(){
        return new Observable<string>(observer => {
            this.socket.on("getQuestion",(data:string) => {
                observer.next(data);
            })
        });
    }

    //
    validAnswer(answer:string){
        this.socket.emit("validAnswer",{answer:answer,roomID:this.roomID});
    }

    //
    validAnswerResult(){
        return new Observable<GameDTO>(observer => {
            this.socket.on("validAnswerResult",(data:GameDTO) => {
                observer.next(data);
            })
        })
    }

    //
    canDraw(){
        return new Observable(observer => {
            this.socket.on("canDraw",(data:boolean) => {
                observer.next(data);
            })
        })
    }

    //
    beginGame(){
        this.socket.emit("beginGame",{roomID:this.roomID});
    }

    //
    gameOver(){
        return new Observable(observer => {
            this.socket.on("gameOver",() => {
                observer.next();
            })
        });
    }

    //
    updateFriendList(){
        return new Observable(observer => {
            this.socket.on("updateFriendList",() => {
                observer.next();
            });
        });
    }

    //
    clearCanvas(){
        return new Observable(observer => {
            this.socket.on("clearCanvas",() => {
                observer.next();
            })
        })
    }

    //
    // sendRequestToFriend(user:string){
    //     this.socket.emit("sendRequestToFriend",{user:this.user.getUserInfo().user,targetUser:user,first:this.organize.getOrganizeFirst()});
    // }

    //
    getRequest(){
        return new Observable(observer => {
            this.socket.on("getRequest",(data:{user:string,first:string}) => {
                observer.next(data);
            })
        });
    }

    //
    updateInfo():Observable<any>{
        return new Observable(observer => {
            this.socket.on("updateInfo",data => {
                observer.next(data);
            })
        });
    }
    
    //
    getMsg(){
        return new Observable(observer => {
            this.socket.on("userMsg",(data:any) => {
                observer.next({msg:data.msg, tag:data.tag});
            });
        });
    }

    //
    getRightAnswer():Observable<Response>{
        return this.http.post(this.configService.exportInterface("/getRightAnswer"),{roomID:this.roomID})
    }

    // //
    // nextGame(){
    //     this.http.post(this.configService.exportInterface("/nextGame"),{roomID:this.roomID})
    //         .subscribe(() => {})
    // }

    //
    updateReadyQueue(){
        this.http.post(this.configService.exportInterface("/updateReadyQueue"),{user:this.user.user, roomID:this.roomID})
            .subscribe(() => {})
    }
}


